import {Component} from '@angular/core';
import {RequestStatus} from '../../shared';
import {SubscriptionRequest} from '../api'
import {AccountService} from '../account.service';
import {Router} from '@angular/router';
import {FormControl, Validators} from '@angular/forms';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['../styles.scss', './sign-up.component.scss']
})
export class SignUpComponent {

  model = new SubscriptionRequest();
  formStatus: RequestStatus = RequestStatus.IDLE;
  serverMessage: string;
  today: Date;

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
  ]);

  constructor(private accountService: AccountService, private router: Router) {
    this.serverMessage = '';
    this.today = new Date();
  }

  onSubmit() {
    this.formStatus = RequestStatus.LOADING;
    this.accountService.signup(this.model).subscribe(
      () => {
        this.formStatus = RequestStatus.SUCCESS;
        this.router.navigate(['/account/checkmail']);
      }, (error: string) => {
        this.serverMessage = error;
        this.formStatus = RequestStatus.FAILURE;
      });

  }

}



