import {Component} from '@angular/core';
import {RequestStatus} from '../../shared';
import {SignRequest} from '../api';
import {AuthService} from '../../core/auth.service';
import {Router} from '@angular/router';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['../styles.scss', './login.component.scss']
})
export class LoginComponent {

  model = new SignRequest(undefined, undefined);
  formStatus: RequestStatus = RequestStatus.IDLE;
  serverMessage: string;

  constructor(public authService: AuthService, public router: Router) {
    this.serverMessage = '';
  }

  onSubmit() {
    this.formStatus = RequestStatus.LOADING;
    this.authService.login(this.model).subscribe((success: string) => {
      this.serverMessage = JSON.stringify(success) + ' you will now be redirected to your account..';
      this.formStatus = RequestStatus.SUCCESS;
      // Get the redirect URL from our auth service & Redirect the user
      this.router.navigate([this.authService.redirectUrl]);
    }, error => {
      this.serverMessage = error.message;
      this.formStatus = RequestStatus.FAILURE;
    });
  }

}



