import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {HttpModule} from '@angular/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {
  MatInputModule,
  MatRadioModule,
  MatButtonModule,
  MatIconModule,
  MatTabsModule,
  MatProgressSpinnerModule,
  MatDatepickerModule,
  MatNativeDateModule, MatCardModule, MatSelectModule
} from '@angular/material';
import {FlexLayoutModule} from '@angular/flex-layout';
import {AccountRoutingModule} from './account-routing.module';
import {SignUpComponent} from './sign-up/sign-up.component';
import {LoginComponent} from './login/login.component';
import {MailValidationComponent} from './mail-validation/mail-validation.component';
import {AccountService} from './account.service';
import {UiComponentsModule} from '../ui-components/ui-components.module';
import {AuthService} from '../core/auth.service';
import {Router} from '@angular/router';
import { CheckYourMailComponent } from './check-your-mail/check-your-mail.component';

@NgModule({
  imports: [
    CommonModule,
    AccountRoutingModule,
    HttpModule,
    FormsModule,
    MatInputModule,
    MatRadioModule,
    MatButtonModule,
    MatIconModule,
    MatTabsModule,
    MatCardModule,
    MatSelectModule,
    MatProgressSpinnerModule,
    MatDatepickerModule,
    MatNativeDateModule,
    FlexLayoutModule,
    UiComponentsModule,
    ReactiveFormsModule
  ],
  declarations: [ LoginComponent, MailValidationComponent, SignUpComponent, CheckYourMailComponent ],
  providers: [ AccountService ]
})
export class AccountModule {

  constructor(private authService: AuthService, private router: Router) {
    // No access to account module pages if you're already logged in
    if( authService.isLoggedIn ) {
      this.router.navigate(['/timeline']);
    }
  }
}



