import {Injectable} from '@angular/core';
import {Headers, Http, Response} from '@angular/http';

import {Observable} from 'rxjs/Observable';
import {ErrorObservable} from 'rxjs/observable/ErrorObservable';

import {SubscriptionRequest} from './api/subscription-request';
import {environment} from '../../environments/environment';
import {ValidateMailRequest} from './api/validate-mail-request';

@Injectable()
export class AccountService {

  HEADER = {headers: new Headers({'Content-Type': 'application/json' }) };

  constructor(private http: Http) {
  }

  signup(subscriptionRequest: SubscriptionRequest): Observable<any> {
    return this.http.post(environment.servicesUrls.accountService + 'account/', subscriptionRequest, this.HEADER)
    // TODO delete the then because it has no real use
               .map((response: Response) => {
                 console.log(response.json());
               })
               .catch(this.handleError);
  }

  validateMail(validateMailRequest: ValidateMailRequest): Observable<any> {
    return this.http.put(environment.servicesUrls.accountService + 'account/validation', validateMailRequest, this.HEADER)
               .catch(this.handleError);
  }

  private handleError(error: Response | any): Observable<string> {
    // TODO use a remote logging infrastructure in the futur
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return ErrorObservable.create(errMsg);
  }

}



