import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {SignUpComponent} from './sign-up/sign-up.component';
import {LoginComponent} from './login/login.component';
import {MailValidationComponent} from './mail-validation/mail-validation.component';
import {CheckYourMailComponent} from './check-your-mail/check-your-mail.component';

const AccountRoutes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'mail-validation/:mailValidationToken',
    component: MailValidationComponent
  },
  {
    path: 'signup',
    component: SignUpComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'checkmail',
    component: CheckYourMailComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(AccountRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class AccountRoutingModule {
}



