import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Params, Router} from '@angular/router';

import {AccountService} from '../account.service';
import {RequestStatus} from '../../shared';
import {ValidateMailRequest} from '../api';

import 'rxjs/add/operator/switchMap';

// Example route : http://localhost:4200/account/mail-validation/523b9d09-6508-4a47-81c7-eedf9650c86e
@Component({
  selector: 'app-mail-validation',
  templateUrl: './mail-validation.component.html',
  styleUrls: [ './mail-validation.component.scss' ]
})
export class MailValidationComponent implements OnInit {

  serverResponseStatus: RequestStatus = RequestStatus.LOADING;
  serverResponseMessage = '';

  constructor(private activatedRoute: ActivatedRoute, private accountService: AccountService, private router: Router) {
  }

  ngOnInit() {
    this.activatedRoute.params
      .switchMap((params: Params) => {
        this.serverResponseStatus = RequestStatus.LOADING;
        return this.accountService.validateMail(new ValidateMailRequest(params[ 'mailValidationToken' ]));
      })
      .subscribe(() => {
        this.serverResponseStatus = RequestStatus.SUCCESS;
        this.serverResponseMessage = 'Your email account has been confirmed, you can now use Timeline !';
      }, (error: string) => {
        this.serverResponseStatus = RequestStatus.FAILURE;
        this.serverResponseMessage = error;
      });
  }

  redirectToTimeline() {
    this.router.navigate(['/timeline'])
  }
}



